<template>
  <div class="AboutMeForm tr">
    <img src="../images/component/AboutMeForm/form_bg.svg" width="480px;" height="450px;" class="AboutMeForm__bg">
    <div class="AboutMeForm__add">
      <img src="../images/component/AboutMeForm/add.png" class="AboutMeForm__add__icon" width="15px" height="15px">
      <p class="AboutMeForm__add__title">添加我的背景</p>
