      <img src="../images/component/AboutMeForm/pre.svg" width="50px" height="32px;" >
      <p class="AboutMeForm__input__p">上一个</P>
    </div>
    <div class="AboutMeForm__input__next">
      <img src="../images/component/AboutMeForm/next.svg" width="50px" height="32px;" >
      <p class="AboutMeForm__input__p">下一个</p>
    </div>
    <router-link to="/SelfAssessment/AboutMe/PE" class="AboutMeForm__button" tag='div'>
      <qybutton small>下一步</qybutton>
    </router-link>
  </div>
</template>

<script>
import dropdown from './dropdown'
import qybutton from './Button.vue'
export default {
  name: 'AboutMeForm',
  components: {
    dropdown,
    qybutton
  },
  data () {
    return {
      InputHand: false,
      schoolList: ['纽约大学(NewYork Uni..)', '纽约大学(NewYork Uni..)', '纽约大学(NewYork Uni..)', '纽约大学(NewYork Uni..)', '查看更多(more)'],
      zhanList: ['经济', '管理', '会计', '金融', '数学', '统计', '电子电气', '通信工程', '计算机', '传播学', '艺术', '体育'],
      year: ['2008', '2009', '2010', '2011', '2012', '2013', '2014', '2015', '2016', '2017'],
      season: ['春季', '夏季', '秋季', '冬季']
    }
  }

}
</script>

<style scoped>

.tr{
  animation: sc 1s linear ;
}
@keyframes sc{
  from{
    transform: scaleY(0)
  }
  to{
    transform: scaleY(100%)
  }
}


.AboutMeForm{
  width: 480px;
  height: 450px;
  position: relative;
}
.AboutMeForm__bg{
  position: absolute;
  left: 0;
}
.AboutMeForm__add{
  width: 160px;
  height: 40px;
  border: 1px dashed rgba(177, 217, 218, 0.5) ;
  position: absolute;
  left: 30%;
  top: 24px;
  cursor: pointer;
}

.AboutMeForm__add:hover{
  background-color: rgba(177, 217, 218, 0.08);
}
.AboutMeForm__add__icon{
  padding: 12px 18px;
  float: left;
}
.AboutMeForm__add__title{
  color: rgb(177, 217, 218);
  text-align: center;
  line-height: 40px;
}
.AboutMeForm__delete{
  position: absolute;
  right: 50px;
  top: 24px;
  width: 45px;
  height: 45px;
  border: 1px solid rgba(177, 217, 218, 0.5);
  border-radius: 50%;
  cursor: pointer;
}
.AboutMeForm__delete:hover{
  background-color: rgba(177, 217, 218, 0.3);
}
.AboutMeForm__delete__icon{
  width: 15px;
  height: 15px;
  padding: 15px;
}
.AboutMeForm__input{
  width: 280px;
  height: 300px;
  background-color: rgba(177, 217, 218, 0.08);
  position: absolute;
  top: 80px;
  left: 22%;
}

.AboutMeForm__input__Time{
  margin: 20px;
}
.AboutMeForm__input__Time span{
  font-size: 12px;
  color: rgb(177, 217, 218)
}
.AboutMeForm__input__Time--season{
  position: relative;
  float: left;
}
.AboutMeForm__input__Time--year{
  position: relative;
  float: left;
  margin-left: 20px;
}
.AboutMeForm__button{
  position: absolute;
  bottom: 30px;
  left: 45%;
}

.AboutMeForm__input__pre{
  position: absolute;
  left: 20px;
  top: 50%;
  cursor: pointer;
  color:rgba(177, 217, 218,0.5);
}
.AboutMeForm__input__pre:hover{
  color:rgb(177, 217, 218);
}
.AboutMeForm__input__next{
  position: absolute;
  right: 20px;
  top: 50%;
  cursor: pointer;
  color:rgba(177, 217, 218,0.5);
}
.AboutMeForm__input__next:hover{
  color:rgb(177, 217, 218);
}
.AboutMeForm__input__p{
  font-size: 12px;
  margin-top: 12px;
  margin-left: 5px;
}

</style>



